@import "../../../styles/colors";

.type-column {
  min-width: 50px;
  max-width: 50px;
}

.name-column {
  width: 25%;
}

.account-column {
  width: 15%;
}

.link-column {
  width: 20%;
  
  &-text {
    display: inline-block;
    position: relative;
    color: $blue;    
    cursor: pointer;
    border-bottom: 1px dashed $blue;

    &:before {
      content: '';
      position: absolute;
      top: -3px;
      left: -30px;
      width: 30px;
      height: 30px;
      background: url('./url.svg') no-repeat 0 center;
    }

    &:hover {
      border-bottom: 1px dashed transparent;
    }
  }
}
.switch-column {
  width: 10%;
}
.date-column,
.count-column {
  width: 20%;
}
