@import "../styles/colors";

.constructor {
  &-page {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
    
  &-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    box-shadow: 2px 1px 4px rgba(0, 0, 0, 0.15);
    flex-shrink: 0;

    &__left {
      display: flex;
      align-items: center;
    }

    &__right {
      display: flex;
      align-items: center;
    }
  }

  &-back {
    position: relative;
    display: block;
    width: 30px;
    height: 30px;
    margin-left: 18px;
    color: #000000;
    text-decoration: none;
    background: url(/img/home.svg) 5px 8px no-repeat;

    &:after {
      content: '';
      position: absolute;
      top: -15px;
      right: -15px;
      width: 0;
      height: 100px;
      border-right: 0.5px solid $lightgrey;
    }
  }

  &-title {
    position: relative;
    margin: 19px 10px 16px 37px;
    padding: 2px 2px 2px 4px;
    border: 1px solid transparent;
    border-radius: 5px;

    &:after {
      content: '';
      position: absolute;
      top: -20px;
      right: -25px;
      width: 0;
      height: 100px;
      border-right: 0.5px solid $lightgrey;
    }

    &:hover {
      border: 1px solid $grey;
      background: $white;

      input {
        background: $white;
      }

      &:before {
        display: none;
      }

      .constructor-title__tooltip {
        display: block;
      }
    }

    &_focus {
      border: 1px solid $blue;
      background: $white;

      input {
        background: $white !important;
      }

      &:before {
        display: none;
      }

      .constructor-title__tooltip {
        display: none;
      }
      

      &:hover {
        .constructor-title__tooltip {
          display: none;
        }
      }
    }

    &__tooltip {
      display: none;
      position: absolute;
      padding: 5px 10px;
      background: $blue;
      color: $white;
      top: 35px;
      left: 50%;
      transform: translateX(-50%);
      font-size: 12px;
      border-radius: 5px;
      z-index: 1;

      &:before {
        content: '';
        position: absolute;
        left: 50%;
        margin-left: -5px;
        top: -7px;
        width: 0; 
        height: 0; 
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 10px solid $blue;
      }
    }

    &__input input{
      border: none;
      outline: none;
    }

    &__info {
      position: absolute;
      top: 20px;
      left: 0;
      background: $blue;
      color: $white;
    }

    &_elemmenu {
      margin: 10px auto 40px;
      width: fit-content;

      input {
        background: $lightgrey;
      }
    }
  }

  &-titlebg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    background: $lightgrey;
    z-index: -1;
  }

  &-status {
    position: relative;
    margin-right: 23px;
    padding: 20px 16px 16px 30px;
    width: 115px;
    background: url(/img/check.svg) 0 21px no-repeat;
    color: $grey;

    &_saving {
      color: $darkgrey;
      background: none;
    }
  }

  &-settings {
    position: relative;
    display: block;
    margin-left: 43px;
    padding: 20px 16px 16px 27px;
    width: 115px;
    background: url(/img/settings.svg) 0 21px no-repeat;
    text-decoration: none;
    color: $darkgrey;

    &:after {
      content: '';
      position: absolute;
      top: -15px;
      right: -15px;
      width: 0;
      height: 100px;
      border-right: 0.5px solid $lightgrey;
    }
  }

  &-steps {
    margin: 10px 0;
    position: relative;

    &__button {
      background: transparent;
      border: 0;
      width: 30px;
      height: 30px;
      cursor: pointer;
      margin: 0 5px;

      &:hover {
        & + .constructor-title__tooltip {
          display: block;
        }
      }

      &:disabled {
        opacity: 0.2;
      }
    }
  }

  &-preview {
    padding: 3px 3px 0;

    &__button {
      padding: 7px 15px 6px 32px;
      border-radius: 5px;
      border: 0.5px solid $grey;
      background: $white url(/img/preview.svg) 12px center no-repeat;
      color: $black;
      cursor: pointer;

      &:hover {
        border: 0.5px solid $grey3;
      }
    }
  }

  &-publish {
    padding: 3px 16px 0;

    &__button {
      padding: 7px 20px 6px 37px;
      border-radius: 5px;
      border: 1px solid $blue;
      background: $blue url(/img/publish.svg) 16px center no-repeat;
      color: $white;
      cursor: pointer;

      &:hover {
        background: $darkblue url(/img/publish.svg) 16px center no-repeat;
        border: 1px solid $darkblue;
      }
    }
  }

  &-content {
    height: 100%;
    position: relative;
  }

  &-work {
    position: relative;
    height: 100%;
    background: $lightgrey;
    overflow-x: hidden;
    overflow-y: overlay;
  }  

  &-row {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
  }

  &-options {
    position: fixed;
    display: flex;
    justify-content: space-around;
    box-shadow: 0px -2px 4px rgba(0, 0, 0, 0.15);
    bottom: 50px;
    left: 40%;
    padding: 0 10px;
    line-height: 50px;
    background: #FFFFFF;
    border-radius: 5px;

    &__resize {
      cursor: pointer;
    }

    &__scale {
      margin: 0 0 0 20px;
      width: 40px;
    }

    &__total {
      margin: 0 10px;
    }

    &__button {
      background: $darkgrey;
      border-radius: 5px;
      padding: 0 10px;
      text-align: center;
      height: 36px;
      line-height: 36px;
      margin-top: 6px;
      cursor: pointer;
      white-space: nowrap;
      color: $white;
    }
  }
}