.checkbox {
  position: relative;
}

.checkbox-custom {
  width: 26px;
  height: 26px;
  border: 1px solid #00baff;
  border-radius: 3px;
  overflow: hidden;
  color: #00baff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.checkbox-label {
  cursor: pointer;
  margin-bottom: 0;
  vertical-align: bottom;
}

.checkbox-input {
  opacity: 0;
  position: absolute;
}