.tabs {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
  border-bottom: 1px solid rgba(207, 207, 207, 0.7);
  margin-bottom: 20px;
}

.tabs-item {
  font-size: 14px;
  margin-right: 20px;
  color: #071F27 !important;
  font-size: 14px;
  padding: 20px 0 20px;
  border-bottom: 4px solid transparent;
  transition: color 250ms ease-in-out, border-color 250ms ease-in-out;
  cursor: pointer;
}

.tabs-item_active {
  color: #00baff !important;
  border-bottom-color: #00baff;
}

.mailing-icon-column {
  min-width: 85px;
  max-width: 85px;
  text-align: center;
  color: #929292;

  display: none;

  @media screen and (min-width: 1500px) {
    display: block;
  }
}

.mailing-name-column {
  width: 200px;

  @media screen and (min-width: 1500px) {
    width: calc(100% - 950px);
  }
}

.mailing-date-column {
  min-width: 210px;
  white-space: nowrap;
}

.mailing-count-column {
  width: 125px;
}

.mailing-clicks-column {
  min-width: 80px;
}