.switch {
  margin-bottom: 0;
  color: #00baff;
}

.switch-disabled {
  color: #C4C4C4;
}

.switch-caption {
  font-weight: 500;
  margin-bottom: 16px;
}

.switch-control {
  vertical-align: bottom;
  
  > div:first-child {
    border-radius: 5px !important;
    background-color: #00baff !important;
    height: 30px !important;
  }

  > div:nth-child(2) {
    border-radius: 5px !important;
    height: 28px !important;
    height: 28px !important;

    position: relative;

    &::before {
      content: "on";
      display: inline-block;
      position: absolute;
      top: 0;
      left: 0;
      color: inherit;
      font-size: 14px;
      font-weight: 500;

      transition: color 250ms ease-in-out;
      line-height: 26px;

      width: 100%;
      text-align: center;

      .switch-disabled & {
        content: "off";
      }
    }
  }

  .switch-disabled & {
    > div:first-child {
      background-color: #C4C4C4 !important;
    }
  }
}
