.flow {
  &-block {    
    font-size: 10px !important;

    &__title {
      position: relative !important;
      font-size: 12px !important;
      line-height: 15px !important;
      padding: 0 0 0 35px !important;
      margin: 10px 0 17px !important;
      color: $grey2 !important;

      &:before {
        content: '' !important;
        position: absolute !important;
        top: -5px !important;
        left: 0 !important;
        border-radius: 5px !important;
        width: 24px !important;
        height: 24px !important;
        background: $blue !important;
      }

      &_start {
        text-align: left !important;
        padding: 0 0 0 40px !important;
        margin: 9px 0 20px !important;
        color: $white !important;
        
        &:before {
          background: $blue url(/img/start.svg) 0px 0px no-repeat !important;
          top: -7px !important;
          left: 4px !important;
        }
      }

      &_message {
        color: $blue !important;

        &:before {
          background: $blue url(/img/message_hover.svg) center no-repeat !important;
          background-size: 17px !important;
        }
      }

      &_action {
        color: $orange !important;

        &:before {
          background-color: $orange !important;
          background: $orange url(/img/action_hover.svg) center no-repeat !important;
          background-size: 17px !important;
        }
      }

      &_condition {
        color: $green !important;

        &:before {
          background: $green url(/img/condition_hover.svg) center  no-repeat !important;
          background-size: 16px !important;
        }
      }

      &_delay {
        color: $violet !important;

        &:before {
          background: $violet url(/img/delay_hover.svg) center no-repeat !important;
          background-size: 16px !important;
        }
      }
    }

    &__placeholder {
      background: url(/img/button.svg) center 0 no-repeat !important;
      background-size: 233px !important;
      text-align: center !important;
      padding: 15px 0 18px 0 !important;
      margin-bottom: 20px !important;

      &_question {
        margin-bottom: 0px !important;
      }

      &:hover {
        background: url(/img/button_hover.svg) center 0 no-repeat !important;
        background-size: 233px !important;
        color: $black !important;
      }

      &_answer {
        padding: 8px 0 !important;
        background: url(/img/button_placeholder.svg) center 0 no-repeat !important;
        background-size: 212px !important;

        &:hover {
          background: url(/img/button_placeholder.svg) center 0 no-repeat !important;
          background-size: 212px !important;
        }
      }
    }
    
    &__message {
      position: relative !important;
      margin-bottom: 20px !important;
      word-break: break-all !important;

      &_text, &_buttons {
        padding: 9px 10px !important;
        border-radius: 5px !important;
        background: $lightgrey !important;

        .flow-block__placeholder_message {
          background: url(/img/button_placeholder.svg) center 0 no-repeat !important;
          background-size: 212px !important;
          padding: 8px 0 !important;
          margin-bottom: 0px !important;

          &:hover {
            background: url(/img/button_placeholder_hover.svg) center 0 no-repeat !important;
            background-size: 212px !important;
          }
        }
      }
    }

    &__question {
      position: relative !important;
      margin-bottom: 10px !important;
      white-space: break-spaces !important; 

      &_text, &_answers {
        padding: 10px !important;
        margin-bottom: 14px !important;
        border-radius: 5px !important;
        background: $lightgrey !important;

        .flow-block__placeholder_message {
          background-size: 212px !important;

          &:hover {
            background-size: 212px !important;
          }
        }
      }
    }

    &__answerwait {
      position: relative !important;
      padding: 9px 10px 10px 30px !important;
      border-radius: 10px !important;
      margin-bottom: 20px !important;
      background: $lightblue url(/img/answer.svg) 7px center  no-repeat !important;
    }

    &__buttons, &__answers {
      margin-top: 10px !important;
    }
    
    &__button, &__answer {
      position: relative !important;
      background: $white !important;
      border: 1px solid transparent !important;
      border-radius: 5px !important;
      margin-bottom: 10px !important;
      text-align: center !important;
      padding: 6px 0 !important;
      font-size: 10px !important;

      &:hover {
        border: 1px solid $blue !important;
      }

      &:last-child {
        margin-bottom: 0 !important;
      }
    
      .react-flow__handle {
        top: -5px !important;
        right: 15px !important;
      }
    }

    &__file {
      text-align: center !important;
      padding: 8px 0 8px 16px !important;
      background: $lightgrey !important;
      margin-bottom: 23px !important;
      border-radius: 5px !important;
    }

    &__filename {
      display: inline-block;
      position: relative;

      &:before {
        content: '';
        position: absolute;
        width: 14px;
        height: 14px;
        left: -23px;
        top: 2px;
        background: url(/img/clip.svg) no-repeat !important;
        background-size: 14px !important;
      }
    }

    &__images {
      text-align: center !important;
    }

    &__img {
      max-width: 100% !important;
      margin: 0 0 20px 0 !important;
      border: 1px solid $blue;
      border-radius: 10px;  
    }
    
    &__copy {
      display: none !important;
      opacity: 0 !important;
      position: absolute !important;
      width: 30px !important;
      height: 30px !important;
      top: -40px !important;
      left: 50% !important;
      margin-left: 10px !important;
      background: url(/img/copy_shadow.svg) center no-repeat !important;

      &:hover {
        background: url(/img/copy_shadow_hover.svg) center no-repeat !important;
      }
    }
    
    &__delete {
      display: none !important;
      opacity: 0 !important;
      position: absolute !important;
      width: 30px !important;
      height: 30px !important;
      top: -40px !important;
      left: 50% !important;
      margin-left: -30px !important;
      background: url(/img/delete_shadow.svg) center no-repeat !important;
      
      &:hover {
        background: url(/img/delete_shadow_hover.svg) center no-repeat !important;
      }
    }

    &__actions {
      &-item {
        padding:0 10px 10px 10px !important;
        border-bottom: 1px solid $grey !important;

        &:last-child {
          border-bottom: none !important;
        }
      }
    }
    
    &__addTag, &__addField, &__increaseField, &__sendEmail, &__subscribe  {
      padding: 10px 0 10px 20px !important;
      background: url(/img/tag.svg) 0 10px no-repeat !important;
      background-size: 15px !important;
    }

    &__removeTag, &__removeField, &__decreaseField, &__unsubscribe, &__stop {
      padding: 10px 0 10px 20px !important;
      background: url(/img/tag_remove.svg) 0 10px no-repeat !important;
      background-size: 15px !important;
    }

    &__tags {
      display: flex !important;
      flex-wrap: wrap !important;
    }

    &__tag {
      margin-right: 10px !important;
      color: $darkgrey !important;

      &_remove {
        text-decoration: line-through !important;
      }
    }

    &__fields {
      display: flex !important;
      flex-wrap: wrap !important;
    }

    &__field {
      margin-right: 10px !important;
      color: $darkgrey !important;
    }

    &__conditions {
      &-item {
        margin-bottom: 10px !important;
      }

      &-label {
        margin: 0 5px !important;
        color: $grey !important;
      }
    }

    &__nextstep {
      text-align: right !important;
      font-size: 10px !important;
      color: $grey2 !important;
    }
  }
}

.react-flow {
  &__controls {
    &-button {
      width: 32px !important;
      height: 32px !important;
    }
  }
  
  &__node {
    width: 260px !important;
    padding: 9px 12px !important;
    border-radius: 10px !important;
    background-color: rgba($white, .8) !important;
    border: 1px solid $grey !important;

    &.selected {

      &:before {
        content: '';
        position: absolute;
        top: -40px;
        left: 0;
        width: 100%;
        height: 40px;
      }

      .flow-block__copy {
        display: block !important;
      }

      .flow-block__delete {
        display: block !important;
      }

      &:hover {
        .flow-block__copy, .flow-block__delete {
          opacity: 1 !important;
          transition: opacity 0s linear 0.5s !important;
        }
      }
    }

    &-input {
      background: $blue !important;
      color: $white !important;
      box-shadow: 2px 3px 4px rgba(0, 0, 0, 0.15) !important;

      .flow-block__nextstep {
        color: $white !important;
      }
    }

    &-messageNode {
      &.selected {
        border: 1px solid $blue !important;
        .react-flow__handle {
          &:before {
            border: 1px solid $blue !important;
          }

          &:after {
            content: '' !important;
            position: absolute !important;
            top: 12px !important;
            left: 2px !important;
            width: 5px !important;
            height: 5px !important;
            border-radius: 5px !important;
            background: $blue !important;
            z-index: 4 !important;
          }

          &-right {
            &:after {
              top: 11px !important;
              left: -2px !important;
            }
          }
        }
      }
    }

    &-actionNode {
      &.selected {
        border: 1px solid $orange !important;

        .react-flow__handle {
          &:before {
            border: 1px solid $orange !important;
          }

          &:after {
            content: '' !important;
            position: absolute !important;
            top: 12px !important;
            left: 2px !important;
            width: 5px !important;
            height: 5px !important;
            border-radius: 5px !important;
            background: $orange !important;
            z-index: 4 !important;
          }

          &-right {
            &:after {
              top: 11px !important;
              left: -2px !important;
            }
          }
        }
      }
    }

    &-conditionNode {
      &.selected {
        border: 1px solid $green !important;

        .react-flow__handle {
          &:before {
            border: 1px solid $green !important;
          }

          &:after {
            content: '' !important;
            position: absolute !important;
            top: 12px !important;
            left: 2px !important;
            width: 5px !important;
            height: 5px !important;
            border-radius: 5px !important;
            background: $green !important;
            z-index: 4 !important;
          }

          &-right {
            &:after {
              top: 11px !important;
              left: -2px !important;
            }
          }
        }
      }
    }

    &-delayNode {
      &.selected {
        border: 1px solid $violet !important;

        .react-flow__handle {
          &:before {
            border: 1px solid $violet !important;
          }

          &:after {
            content: '' !important;
            position: absolute !important;
            top: 12px !important;
            left: 2px !important;
            width: 5px !important;
            height: 5px !important;
            border-radius: 5px !important;
            background: $violet !important;
            z-index: 4 !important;
          }

          &-right {
            &:after {
              top: 11px !important;
              left: -2px !important;
            }
          }
        }
      }
    }
  }

  &__handle {
    width: 0 !important;
    height: 30px !important;
    border: none !important;
    transform: translateY(5px) !important;
    background: none !important;

    &:before {
      content: '' !important;
      position: absolute !important;
      border: 0.5px solid $grey2 !important;
      background: $white !important;
      border-radius: 12px !important;
      width: 7px !important;
      height: 7px !important;
      z-index: 4 !important;
    }

    &-left {
      top: 0px !important;
      left: -5px !important;

      &:before {
        top: 10px !important;
        left: 0 !important;
      }
    }

    &-right {
      &:before {
        right: -5px !important;
        bottom: 12px !important;
      }
    }
  }

  &__edge {
    &:hover {
      .react-flow__edge-text,
      .react-flow__edge-textbg 
      {
        visibility: visible !important;
        opacity: 1 !important;
        transition: opacity 0s linear 0.5s !important;
      }
    }

    &-path {
      stroke: #5A696F !important;
    }

    &-text {
      opacity: 0 !important;
      visibility: hidden !important;
      font-size: 24px !important;
      cursor: pointer !important;
    }

    &-textbg {
      opacity: 0 !important;
      visibility: hidden !important;
      cursor: pointer !important;
    }
  }

  &.connection {
    .react-flow__handle-left {
      width: 100% !important;
      height: 100% !important;
      z-index: 10 !important;
    }
  }
}

.react-flow__arrowhead polyline {
  stroke: #5A696F !important;
}

.react-flow__edge {
  &.messageLink.active .react-flow__edge-path{
    stroke: $blue !important;
  }
  &.actionLink.active .react-flow__edge-path{
    stroke: $orange !important;
  }
  &.conditionLink.active .react-flow__edge-path{
    stroke: $green !important;
  }
  &.delayLink.active .react-flow__edge-path{
    stroke: $violet !important;
  }
}

.react-draggable-transparent-selection {  
  .react-flow__node.selected {
    opacity: 0.8 !important;
  }
}