.filter {
  &-detail {
    position: relative;

    &-back {
      position: absolute;
      left: 0px;
      top: 0px;
      width: 35px;
      height: 35px;
      text-align: center;
      font-size: 24px;
      line-height: 32px;
      cursor: pointer;
    }

    &-title {
      text-align: center;
      padding: 5px;
      border-bottom: 1px solid #D0D0DA;
    }

    &-content {
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid #D0D0DA;
    }

    &-col {
      width: 200px;

      &_padding {
        padding: 10px;
      }
    }

    &-list {
      min-width: 170px;
      height: 100%;
      border-right: 1px solid #D0D0DA;
    }

    &-item {
      padding: 10px;
      font-size: 14px;
      cursor: pointer;

      &_active {
        background: #F4F4F4;
        color: #00BAFF;
        box-shadow: 2px 0 0 0px #00BAFF;
      }

      &:hover {
        background: #F4F4F4;
      }
    }
  }
}
