@import "../../../styles/colors";

.button {
  display: inline-flex;
  align-items: center;
  border-radius: 5px;
  background: #00BAFF;
  color: #fff;
  border: 1px solid #00BAFF;
  padding: 5px 20px;
  text-decoration: none;
  cursor: pointer;
  margin: 10px;

  &:hover {
    background: #009ed9;
    border: 1px solid #0095cc;
  }

  &_xl {
    padding: 10px 40px;
    margin-top: 30px;
  }

  &_cancel {
    background: #fff;
    color: #ccc;
    border: 1px solid #ccc;

    &:hover {
      background: #fff;
      color: #009ed9;
      border: 1px solid #0095cc;
    }
  }

  &_remove {
    background: #FF5050;
    color: #fff;
    border: 1px solid #FF5050;

    &:hover {
      background: #FF5050;
      color: #fff;
      border: 1px solid #FF5050;
    }
  }

  &_password {
    background: $white;
    color: $black;
    border: 1px solid $black;
    margin: 0;

    &:hover {
      background: $white;
      color: $grey;
      border: 1px solid $grey;
    }
  }

  &_payment {
    background: $black;
    color: $white;
    border: 1px solid $black;

    &:hover {
      background: $grey;
      color: $black;
      border: 1px solid $grey;
    }
  }

  &_placeholder {
    padding: 6px 90px;
    margin-top: 33px;
    font-size: 14px;
  }
}
