.auth {
  &__block {
    width: 590px;
    height: fit-content;
    padding: 20px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    box-shadow: 0 0 10px $grey;
    border-radius: 5px;
  }

  &__field {
    position: relative;
    margin-bottom: 50px;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 50px;
      height: 48px;
      background: $lightgrey url(/img/key.svg) 12px center  no-repeat;
      border: 1px solid $grey;
      border-right: none;
      border-radius: 5px 0 0 5px;
      z-index: 1;
    }

    &_domain {
      &:after {
        content: '.optima.chat';
        position: absolute;
        top: 0;
        right: 0;
        width: 150px;
        height: 48px;
        background: $lightgrey;
        border: 1px solid $grey;
        border-left: none;
        border-radius: 0 5px 5px 0;
        z-index: 1;
        text-align: center;
        line-height: 50px;
      }
    }
  }

  &__back {
    position: absolute;
    right: 10px;
    top: 0;
    text-decoration: none;
    font-size: 36px;
    color: $grey;
  }

  &__title {
    font-size: 18px;
    text-align: center;
    padding-bottom: 10px;
    margin: 0 -20px 50px;
    border-bottom: 1px solid $lightgrey;
  }

  &__label {
    position: absolute;
    top: -25px;
    font-size: 14px;
  }

  &__input {
    position: relative;
    width: 100%;
    padding: 15px 10px 15px 60px;
    font-size: 16px;
    border-radius: 5px;
    background: $white;
    border: 1px solid $grey;
    outline: none;
  }

  &__button {
    position: relative;
    padding: 14px;
    font-size: 14px;
    width: 300px;
    text-align: center;
    margin: -10px auto 30px;
    border-radius: 5px;
    background: $blue;
    color: $white;
    cursor: pointer;
  }

  &__error {
    position: absolute;
    font-size: 12px;
    color: $red;
  }

  &__info {
    text-align: center;
    margin: 0 0 20px;
    font-size: 14px;
  }

  &__text {
    margin: 0 10px;
  }

  &__delimeter {
    margin: 0 10px;
  }

  &__footer {
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid $grey;
    padding-top: 20px;
    margin: 0 -20px;
    text-align: center;
    font-size: 14px;
  }
}