.bot-select {
  width: 100%;
  position: relative;
  text-align: left;

  &-title {
    position: relative;
    padding: 7px 10px 7px;
    border: 1px solid #D0D0D0;
    font-size: 14px;
    border-radius: 5px;
    width: 100%;
    cursor: pointer;

    &:before {
      content: '⌃';
      position: absolute;
      font-size: 16px;
      top: 1px;
      right: 5px;
      transform: scale(1, -1);
    }

    &_open {
      border-radius: 5px 5px 0 0;

      &:before {
        top: 8px;
        transform: scale(1, 1);
      }
    } 
  }

  &-list {
    position: absolute;
    margin: -1px 0 20px;
    border: 1px solid #D0D0D0;
    border-radius: 0 0 5px 5px;
    background: #FFFFFF;
    width: 100%;
    cursor: pointer;
    z-index: 1;

    &_newfield {
      bottom: -27px;
      width: 247px;
      right: 10px;
    }
  }

  &-item {
    font-size: 14px;
    padding: 10px;

    &:hover {
      background: #F4F4F4;
      color: #00BAFF;
    }
  }

  &-form {
    padding: 10px;
  }

  &-text {
    font-size: 14px;
  }

  &-wrap {
    position: absolute;
    top: 0;
    right: 0;
    width: 300px;
    padding: 20px;
    border: 1px solid #cccccc;
    border-radius: 5px;
    background: #fff;
    z-index: 1;

    &__title {
      padding: 10px 0 0 30px;
      margin-bottom: 15px;

      &_addtag {
        background: url(/img/tag.svg) 0 12px no-repeat;
      }

      &_removetag {
        background: url(/img/tag_remove.svg) 0 12px no-repeat;
      }
    }
  }

  &-tags {
    margin-top: 10px;
    min-height: 100px;
  }

  &__input {
    width: 100%;
    padding: 10px;
    border: 1px solid #D0D0DA;
    border-radius: 5px;
    outline: none;
    font-size: 14px;

    &_active {
      border-radius: 5px 5px 0 0;
    }
  }

  &__choice {
    &-list {
      position: absolute;
      margin: -1px 0 20px;
      border: 1px solid #D0D0DA;
      border-radius: 0 0 5px 5px;
      background: #FFFFFF;
      width: 258px;
      cursor: pointer;
      z-index: 1;
    }

    &-item {
      padding: 5px;
      cursor: pointer;
    }

    &-create {
      font-size: 14px;
      padding: 10px;
      text-align: center;
      background: #F4F4F4;
      border-radius: 0 0 5px 5px;
    }
  }

  &-buttons {
    display: flex;
    justify-content: center;
  }
}