.rc-slider {
  height: 18px;

  .rc-slider-rail,
  .rc-slider-track {
    background-color: #34464D;
    height: 8px;
  }

  .rc-slider-step {
    height: 8px;
    top: 36px;
  }

  .rc-slider-dot {
    width: 1px;
    border-radius: 1px;
    height: 10px;
    bottom: 0;
    margin-left: 0;
    border: 0 none;
    background-color: #C4C4C4;
  }

  .rc-slider-handle {
    height: 26px;
    width: 26px;
    margin-top: -10px;
    border-radius: 5px;
    border: 1px solid #00baff;
    box-shadow: 2px 2px 4px rgba(#000000, .15);
    transition: background 250ms ease-in-out;

    &:hover,
    &:active {
      background-color: #F4F4F4;
    }
  }

  .rc-slider-mark {
    font-size: 12px;
    font-weight: 500;
    top: 50px;
  }

  .rc-slider-mark-text {
    max-width: 75px;
    color: #071F27;
    font-size: 14px;

    height: calc(2.5em * 1.25);
    line-height: 1.25;
    display: inline-flex;
    align-items: center;

    &-active {
      color: #071F27;
    }
  }

  .rc-slider-tooltip {
    .rc-slider-tooltip-inner {
      width: 90px;
      height: 90px;
      border-radius: 5px;
      background: linear-gradient(#00baff, #00DDFF);
      box-shadow: 2px 2px 4px rgba(#000000, .15);
    }

    .rc-slider-tooltip-arrow {
      bottom: -1px;
      left: 50%;
      margin-left: -5px;
      border-width: 10px 6px 0;
      border-top-color: #00DDFF;
    }
  }
}