.account-list {
  margin-top: 30px;
  margin-bottom: 40px;
}

.account {
  display: flex;
  justify-content: space-between;
  border: 1px solid #C4C4C4;
  padding: 12px 20px;
  align-items: center;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.account-type {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.account-icon {
  margin-right: 14px;
}

.account-connect-button {
  margin-left: auto;
}

.account-input {
  width: 100%;
  border: 1px solid #ccc;
  padding: 5px;
  border-radius: 5px;
  outline: none;
}

// second step

.account-addition-disclaimer {
  margin-bottom: 20px;
  font-size: 14px;
  display: flex;
  align-items: center;
}

.account-addition-type {
  margin-right: 14px;
  min-width: 30px;
}

.account-addition-pages-disclaimer {
  color: rgba(#071F27, .75);
}

.account-addition-pages-list {
  border: 1px solid #C4C4C4;
  border-radius: 5px;
  padding: 0 20px;
}

.account-addition-pages-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;

  &:not(:last-child) {
    border-bottom: 1px solid rgba(#CFCFCF, .75);
  }
}

.account-addition-pages-connect {
  font-weight: 500;
  margin-left: auto;
}

// tg
.telegram-bot-add-form {
  width: 100%;
}

.telegram-bot-add-form-footer {
  display: flex;
  justify-content: space-between;

  margin-top: 20px;
}

.telegram-bot-add-instruction {
  position: relative;
  margin-top: 24px;

  p {
    margin-bottom: 10px;
  }

  &[data-opened="true"] {
    height: auto;
  }
}

.telegram-bot-add-instruction-header {
  font-size: 14px;
  font-weight: 500;
}
.telegram-bot-add-instruction-readmore {
  padding: 0;
}
.telegram-bot-add-instruction-readmore-btn {
  padding: 0;
  color: rgba(#071F27, .75);
  border-bottom: 1px dashed currentColor;

  &:hover,
  &:focus {
    color: #071F27;
    text-decoration: none;
  }
}
.telegram-bot-add-example {
  font-size: 10px;
}
.telegram-bot-add-token-code {
  display: inline-block;
  color: #FFFFFF;
  background-color: #00baff;
  border-radius: 2px;
  padding: 1px 3px;
}
.telegram-bot-add-token {
  font-weight: 500;
  color: #00baff;
  font-size: 14px;
}

.telegram-bot-add-more {
  color: #071F27;
  text-decoration: underline;
  cursor: pointer;
}