@import "../../../styles/colors";

.sidebar {
  padding: 20px 6px 0 36px;
  box-shadow: 4px 0px 10px 1px rgba(196, 196, 196, 0.4);
  position: relative;
  z-index: 10;

  &__logout {
    position: relative;
    margin: 0 -6px 0 -35px;
    padding: 14px 15px 14px 78px;
    color: inherit;
    background: url(/img/logout.svg) no-repeat 52px center;
    border-top: 1px solid $lightgrey;
    cursor: pointer;

    &:hover {
      color: #00baff;
      background: url(/img/logout_hover.svg) no-repeat 52px center;
    }
  }

  &-item {
    position: relative;
    padding: 14px 0 15px;
    color: inherit;
    white-space: nowrap;
  }
  
  &-is-active {
    color: #00baff;

    svg, path {
      stroke: #00baff;
    }
  }
  
  &-is-iconed {
    padding-left: 43px;
  }
  
  &-link {
    color: inherit;
    text-decoration: none;
  
    &:hover {
      text-decoration: none;
      color: #00baff;

      svg, path {
        stroke: #00baff;
      }
    }

    &_develop {
      &:hover {
        color: inherit;
      }
    }
    &-develop {
      color: #ccc;
      font-size: 12px;
    }
  }
  
  &-icon {
    position: absolute;
    left: 11px;
    width: 28px;
  }
}



