@import "../../../styles/colors";

.constructor {
  &-floatmenu {
    position: absolute;
    background: $white;
    min-width: 160px;
    transform: translate(-133px, -36px);
    border: 1px solid $grey;
    box-shadow: 0px 0.5px 1px rgba(0, 0, 0, 0.38);
    border-radius: 5px;
    z-index: 1001;

    &_addMessage {
      transform: translate(-133px, -36px);
    }

    &_showTemplate {
      transform: translate(-133px, -62px);
    }

    &_nextStep {
      width: 312px;
      transform: translate(-285px, -62px);
    }

    &_buttonNextStep {
      width: 312px;
      transform: translate(-265px, -68px);
    }

    &_answerCreate {
      width: 340px;
      transform: translate(-272px, -99px);
    }

    &_tag, &_name, &_email, &_phone, &_dateTime {
      margin-left: -60px;
    }

    &__close {
      position: absolute;
      top: 0;
      right: 0;
      font-size: 24px;
      width: 35px;
      height: 35px;
      text-align: center;
      cursor: pointer;
      z-index: 1;
    }
    
    &__title {
      padding: 14px;
      text-align: center;
      font-weight: 500;
      border-bottom: 1px solid $lightgrey;
    }
  }
}