.keywords {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 30px;

  &-item {
    position: relative;
    padding: 5px 30px 5px 5px;
    background: #F4F4F4;
    border: 1px solid #D0D0DA;
    border-radius: 5px;
    margin: 5px 5px 0 0;

    &-close {
      position: absolute;
      font-size: 24px;
      line-height: 30px;
      text-align: center;
      width: 30px;
      height: 30px;
      top: 0;
      right: 0;
      cursor: pointer;
    }
  }
}
