@import "../../../styles/colors";

.constructor {
  &-elemmenu {
    position: absolute;
    top: 0;
    right: 0;
    width: 380px;
    height: 100%;
    overflow-x: hidden;
    overflow-y: overlay;
    background: $white;
    border: 1px solid $grey;
    box-shadow: -2px 2px 4px rgba(0, 0, 0, 0.15);
    padding: 0 39px;
    font-size: 14px;
    z-index: 1000;

    &_hidden {
      overflow: hidden;
    }

    &__block {
      position: relative;
      padding: 18px;

      &_title {
        border-bottom: 1px solid $lightgrey;
        padding: 28px 18px;
      }

      &_choice {
        padding: 0 18px 20px;
      }
    }

    &__label {
      margin-bottom: 15px;
      color: $darkgrey;
      font-size: 14px;
      font-weight: 500;

      &_title {
        margin-bottom: 16px;
      }

      &_choice {
        margin: 7px 0 0;
      }

      &_question {
        padding: 2px 0 0 20px;
        margin-bottom: 10px;
        background: url(/img/question.svg) 2px 6px no-repeat;
      }

      &_answer {
        padding: 12px 0 0 24px;
        background: url(/img/answer.svg) 3px 16px no-repeat;
        border-top: 1px solid $grey;
        margin: 12px 0 0;
      }

      &_addTag, &_addField, &_sendEmail, &_subscribe {
        padding: 10px 0 0 30px;
        background: url(/img/tag.svg) 0 12px no-repeat;
        margin-bottom: 15px;
      }

      &_removeTag, &_removeField, &_unsubscribe {
        padding: 10px 0 0 30px;
        background: url(/img/tag_remove.svg) 0 12px no-repeat;
        margin-bottom: 15px;
      }

      &_col {
        flex-basis: 45%;
      }
    }

    &__subtitle {
      padding: 16px;
    }

    &__item {
      position: relative;
      margin-bottom: 46px;

      &:hover {
        .constructor-elemmenu__operations {
          display: block;
        }
      }

      &:before {
        content: '';
        position: absolute;
        top: -40px;
        right: -40px;
        width: 40px;
        height: calc(100% + 40px);
      }

      &:after {
        content: '';
        position: absolute;
        bottom: -30px;
        left: -38px;
        width: 380px;
        height: 1px;
        background: $lightgrey;
      }
    }

    &__match {
      padding-left: 30px;
      background: url(/img/match.svg) 5px center no-repeat;
    }

    &__mismatch {
      padding-left: 30px;
      background: url(/img/mismatch.svg) 5px center no-repeat;
    }

    &__remove {
      position: absolute;
      bottom: 0;
      padding: 10px;
      width: 100%;
      text-align: center;
      color: $blue;
      border-top: 1px solid $lightgrey;
      cursor: pointer;
    }

    &__textarea {
      background: $white;
      border-radius: 10px;
      width: 260px;
      resize: none;
      border: none;
      padding: 10px;
      font-family: 'Montserrat', Arial, Helvetica, sans-serif;
    }

    &__message {
      padding: 14px;
      border-radius: 10px;
      background: $lightgrey;
    }

    &__question {
      position: relative;
      padding: 10px 10px 20px;
      border-radius: 10px;
      background: $lightgrey;
      margin-bottom: 30px;

      &:hover {
        .constructor-elemmenu__operations {
          display: block;
        }
      }
    }

    &__input {
      width: 100%;
      padding: 10px;
      border: 1px solid $grey;
      border-radius: 5px;
      outline: none;
      font-size: 14px;

      &:focus {
        border: 1px solid $grey3;
      }

      &_small {
        width: 60px;
      }

      &_textarea {
        font-family: "Rubik", "Arial", sans-serif;
        padding: 12px 18px;
        height: 44px;
        resize: none;
      }

      &_active {
        border-radius: 5px 5px 0 0;
      } 

      &_error {
        border: 1px solid $red !important;
      }

      &_focused {
        border: 1px solid $blue !important;
      }
    }

    &__url {
      padding: 20px;

      &-error {
        position: absolute;
        font-size: 12px;
        color: $red;
      }
    }

    &__buttons {
      margin-top: 10px;
    }
    
    &__button {
      position: relative;
      background: $white;
      border-radius: 5px;
      margin-bottom: 13px;
      text-align: center;
      color: $black;
      padding: 6px 0;
      border: 1px solid $grey;
      cursor: pointer;

      &:hover {
        border: 1px solid $blue;
        color: $blue;

        .constructor-elemmenu__button-txt {
          border-bottom: 1px dashed $blue;
        }
      }

      &-menu {
        margin-top: -2px;
      }

      &_add {
        width: 165px;
        margin: 15px auto 0px;
        padding: 6px 0 7px;
        background: $lightgrey;
  
        &:hover {
          background: $grey1;
          border: 1px solid $grey;
          color: $black;
        }
      }

      &-txt {
        display: inline-block;
        border-bottom: 1px dashed $grey;
      }

      &-remove {
        position: absolute;
        right: 0;
        top: 0;
        width: 30px;
        height: 100%;
        text-align: center;
        font-size: 24px;
        border-left: 1px solid $grey;
        background: url(/img/delete.svg) center no-repeat;
        background-size: 10px;

        &:hover {
          background: url(/img/delete_hover.svg) center no-repeat;
          background-size: 10px;
          border-left: 1px solid $blue;
        }
      }

      &-item {
        position: relative;
        padding: 11px 10px 10px 48px;
        cursor: pointer;
        border: 1px solid $grey;
        border-radius: 5px;
        margin-bottom: 10px;

        &:before {
          content: '';
          position: absolute;
          width: 40px;
          height: 100%;
          top: 0;
          left: 5px;
        }

        &:hover {
          background: $lightgrey;
          border: 1px solid $grey3;
        }

        &_message {
          &:before {
            background: url(/img/message_floatmenu.svg) center no-repeat;
          }
        }

        &_action {
          &:before {
            background: url(/img/action_floatmenu.svg) center no-repeat;
          }
        }
        
        &_condition {
          &:before {
            background: url(/img/condition_floatmenu.svg) center no-repeat;
          }
        }

        &_delay {
          &:before {
            background: url(/img/delay_floatmenu.svg) center no-repeat;
          }
        }

        &_nextstep {
          &:before {
            background: url(/img/nextstep_floatmenu.svg) center no-repeat;
          }
        }

        &_url {
          &:before {
            background: url(/img/url_floatmenu.svg) center no-repeat;
          }
        }
      }

      &-footer {
        padding: 20px;
        display: flex;
        justify-content: center;
      }

      &-cancel {
        padding: 10px 15px;
        border: 1px solid $grey;
        border-radius: 5px;
        color: #000000;
        background: $white;
        margin: 0 5px;
        cursor: pointer;
      }

      &-submit {
        padding: 10px 15px;
        border: 1px solid $blue;
        border-radius: 5px;
        color: $white;
        background: $blue;
        margin: 0 5px;
        text-align: center;
        cursor: pointer;
      }
    }

    &__add {
      position: relative;
      padding: 10px 5px 11px;
      margin: 62px 0 58px;
      border: 1px solid $grey;
      background: $lightgrey;
      border-radius: 5px;
      text-align: center;
      cursor: pointer;

      &:after {
        content: '';
        position: absolute;
        top: 72px;
        left: -38px;
        width: 380px;
        height: 1px;
        background: $lightgrey;
      }

      &:hover {
        background: $grey1;
      }

      &_content {
        &:hover {
          border: 1px solid $blue;
          background: $white;
          color: $blue;
        }
      }

      &_action {
        &:hover {
          border: 1px solid $orange;
          background: $white;
          color: $orange;
        }
      }

      &_condition {
        &:hover {
          border: 1px solid $green;
          background: $white;
          color: $orange;
        }
      }

      &_mailing {
        width: 200px;
      }

      &-item {
        padding: 10px 18px 9px;
        font-size: 14px;
        cursor: pointer;

        &:hover {
          background: $lightgrey;
        }
      }

      &-title {
        font-size: 12px;
        padding: 10px 5px;
        border-top: 1px solid $grey;
        color: $grey;
      }

      &-label {
        display: block;
        margin: -5px -10px;
        padding: 5px 10px;
        cursor: pointer;
      }

      &-file {
        display: none;
      }
    }

    &__detail {
      position: relative;

      &-back {
        position: absolute;
        left: 0px;
        top: 0px;
        width: 35px;
        height: 35px;
        text-align: center;
        font-size: 24px;
        line-height: 32px;
        cursor: pointer;
      }

      &-title {
        text-align: center;
        padding: 5px;
        border-bottom: 1px solid $grey;
      }

      &-content {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid $grey;
      }

      &-col {
        width: 200px;

        &_padding {
          padding: 10px;
        }

        &_wide {
          width: 300px;
        }
      }

      &-list {
        min-width: 170px;
        height: 100%;
        border-right: 1px solid $grey;
      }

      &-item {
        padding: 10px;
        font-size: 14px;
        cursor: pointer;

        &_active {
          background: $lightgrey;
          color: $blue;
          box-shadow: 2px 0 0 0px $blue;
        }

        &:hover {
          background: $lightgrey;
        }
      }

      .constructor-elemmenu__schedule-item_active {
        color: $blue;
      }
    }

    &__empty {
      text-align: center;
      color: $darkgrey;
      padding: 20px;
    }

    &__fit {
      margin-bottom: 20px;
      position: relative;
      
      &-text {
        border-bottom: 1px dashed $green;
        color: $green;
        cursor: pointer;

        &:hover {
          border-bottom: 1px dashed transparent;
        }
      }
    }

    &__popup {
      display: none;
      position: absolute;
      top: -27px;
      left: 0;
      width: 288px;
      border-bottom: none;
      background: $white;
      color: $darkgrey;
      border-radius: 5px;
      box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.15);
      z-index: 3;

      &_active {
        display: block;
      }

      &-item {
        padding: 10px;
        cursor: pointer;

        &_active {
          color: $blue;
        }

        &:hover {
          background: $lightgrey;
        }
      }
    }

    &__datepicker {
      padding: 10px;
      border: 1px solid $grey;
      border-radius: 5px;
      outline: none;
      font-size: 14px;
      width: 180px;

      &_error {
        box-shadow: 0 0 0 1px $red;
      }
    }

    &__nextstep {
      padding: 12px 5px;
      margin: 20px auto;
      background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='%2355646AFF' stroke-width='1' stroke-dasharray='5%2c 6' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e");
      color: $grey2;
      border-radius: 5px;
      text-align: center;
      cursor: pointer;

      &:hover {
        background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='%23071F27FF' stroke-width='1' stroke-dasharray='5%2c 6' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e");
        background-color: $lightgrey;
        color: $black
      }

      &-menu {
        padding: 28px 20px 20px;
      }

      &-item {
        position: relative;
        padding: 15px 10px 15px 50px;
        cursor: pointer;
        border: 1px solid $grey;
        border-radius: 5px;
        margin-bottom: 10px;
        font-size: 12px;

        &:before {
          content: '';
          position: absolute;
          width: 40px;
          height: 100%;
          top: 0;
          left: 0;
          background: url(/img/message_black.svg) center no-repeat;
        }

        &:hover {
          background: $lightgrey;
          border: 1px solid $grey3;
        }

        &_message {
          &:before {
            background: url(/img/message_floatmenu.svg) center no-repeat;
          }
        }

        &_action {
          &:before {
            background: url(/img/action_floatmenu.svg) center no-repeat;
          }
        }

        &_condition {
          &:before {
            background: url(/img/condition_floatmenu.svg) center no-repeat;
          }
        }

        &_delay {
          &:before {
            background: url(/img/delay_floatmenu.svg) center no-repeat;
          }
        }
      }
    }
  
    &__nextelem {
      margin: 20px auto;
      padding: 5px 5px 5px 50px;
      min-height: 44px;
      position: relative;
      border: 1px solid $grey;
      border-radius: 5px;

      &_message {
        background: url(/img/message_floatmenu.svg) 12px center no-repeat;

        .constructor-elemmenu__nextelem-text {
          color: $blue;
        }

        &:hover {
          background: $lightgrey url(/img/message_floatmenu.svg) 12px center no-repeat;
          border: 1px solid $grey3;

          .constructor-elemmenu__nextelem-close {
            border-left: 1px solid $grey3;
          }
        }
      }

      &_action {
        background: url(/img/action_floatmenu.svg) 12px center no-repeat;

        .constructor-elemmenu__nextelem-text {
          color: $orange;
        }

        &:hover {
          background: $lightgrey url(/img/action_floatmenu.svg) 12px center no-repeat;
          border: 1px solid $grey3;

          .constructor-elemmenu__nextelem-close {
            border-left: 1px solid $grey3;
          }
        }
      }

      &_condition {
        background: url(/img/condition_floatmenu.svg) 12px center no-repeat;

        .constructor-elemmenu__nextelem-text {
          color: $green;
        }

        &:hover {
          background: $lightgrey url(/img/condition_floatmenu.svg) 12px center no-repeat;
          border: 1px solid $grey3;

          .constructor-elemmenu__nextelem-close {
            border-left: 1px solid $grey3;
          }
        }
      }

      &_delay {
        background: url(/img/delay_floatmenu.svg) 12px center no-repeat;

        .constructor-elemmenu__nextelem-text {
          color: $violet;
        }

        &:hover {
          background: $lightgrey url(/img/delay_floatmenu.svg) 12px center no-repeat;
          border: 1px solid $grey3;

          .constructor-elemmenu__nextelem-close {
            border-left: 1px solid $grey3;
          }
        }
      }

      &_url {
        position: relative;
        margin-bottom: 30px !important;
        background: url(/img/url_floatmenu.svg) 12px center no-repeat;

        &:before {
          content: '';
          position: absolute;
          bottom: -28px;
          left: -20px;
          width: 310px;
          border-bottom: 1px solid $lightgrey;
        }
      }

      &_button {
        margin: 0 20px 20px;
      }

      &-text {
        font-size: 14px;
        line-height: 34px;
        color: $black;
      }

      &-label {
        font-size: 15px;
        line-height: 43px;
        color: $black;
      }

      &-close {
        position: absolute;
        right: 0;
        top: 0;
        width: 30px;
        height: 100%;
        text-align: center;
        font-size: 32px;
        line-height: 54px;
        background: url(/img/delete.svg) center no-repeat;
        background-size: 14px;
        color: $grey;
        border-left: 1px solid $grey;
        cursor: pointer;

        &:hover {
          background: url(/img/delete_hover.svg) center no-repeat;
          background-size: 14px;
        }
      }
    }

    &__operations {
      position: absolute;
      top: -44px;
      right: -37px;
      display: none;

      &-item {
        position: relative;
        height: 30px;
        width: 30px;
        cursor: pointer;
        
        &_up {
          background: url(/img/up.svg) center no-repeat;

          &:hover {
            background: url(/img/up_hover.svg) center no-repeat;
          }
        }

        &_down {
          background: url(/img/down.svg) center no-repeat;

          &:hover {
            background: url(/img/down_hover.svg) center no-repeat;
          }
        }

        &_copy {
          background: url(/img/copy.svg) center no-repeat;

          &:hover {
            background: url(/img/copy_hover.svg) center no-repeat;
          }
        }

        &_delete {
          background: url(/img/delete.svg) center no-repeat;

          &:hover {
            background: url(/img/delete_hover.svg) center no-repeat;
          }
        }
      }
    }
  
    &__upload {
      position: relative;

      &-item {
        &_file {
          padding: 17px 5px 17px 62px;
          margin-bottom: 30px;
          background: $lightgrey url(/img/clip.svg) 22px center no-repeat;
          display: flex;
          align-items: center;
          height: 50px;
          line-height: 16px;
        }

        &_img {
          margin-bottom: 30px;

          img {
            border-radius: 5px;
            border: 1px solid $blue;
          }
        }
        
        border-radius: 5px;
        position: relative;
        margin-bottom: 10px;
      }

      &-img {
        display: block;
        max-width: 100%;
        margin: 0 auto;
      }
    }

    &__answer {
      padding: 0 20px;
      max-height: 530px;
      max-width: 362px;
      overflow-y: overlay;

      &-block {
        padding-bottom: 20px;
        border-bottom: 1px solid $lightgrey;
      }

      &-checkbox {
        padding: 5px 5px 5px 40px;
        margin: 20px 0 0;
        background: url(/img/checkbox.svg) 0 center no-repeat;
        background-size: 30px;
        color: $grey;
        font-size: 14px;
        cursor: pointer;

        &_active {
          margin: 20px 0;
          background: url(/img/checkbox_active_blue.svg) 0 center no-repeat;
          background-size: 30px;
          color: $blue;
        }
      }

      &-text {
        font-size: 14px;
      }

      &-ready {
        padding: 5px;
        margin: 20px 0 0;
      }
    }

    &__answers {
      display: flex;
      font-size: 14px;
      flex-wrap: wrap;

      &-item {
        position: relative;
        padding: 5px 30px 5px 5px;
        background: $lightgrey;
        border: 1px solid $grey;
        border-radius: 5px;
        margin: 5px 5px 0 0;

        &-close {
          position: absolute;
          font-size: 24px;
          line-height: 30px;
          text-align: center;
          width: 30px;
          height: 30px;
          top: 0;
          right: 0;
          cursor: pointer;
        }
      }
    }

    &__settings {
      position: relative;
      background: $white;
      border-radius: 5px;
      font-size: 12px;
      padding: 5px;
      border: 1px solid $grey;
      overflow: hidden;
      cursor: pointer;

      &-item {
        display: inline-block;
        white-space: nowrap;
        border-bottom: 1px dashed $darkgrey;
        margin-bottom: 5px;
      }

      &-remove {
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: center;
        right: 0;
        top: 0;
        height: 100%;
        line-height: 100%;
        width: 20px;
        border-top: none;
        border-left: 1px solid $grey;
        font-size: 24px;
        text-align: center;
        background: $white;
        border-radius: 0 5px 5px 0;
      }
    }

    &__choice {
      &-create {
        font-size: 14px;
        padding: 10px;
        text-align: center;
        background: $lightgrey;
        border-radius: 0 0 5px 5px;

        &_border {
          margin: -1px 0 20px;
          border: 1px solid $grey;
          border-radius: 0 0 5px 5px;
          cursor: pointer;
        }
      }
    }

    &__row {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-basis: 45%;

      &_bottom {
        margin-bottom: 20px;
      }

      &_schedule {
        margin-top: 20px;
        align-items: flex-start;
      }

      &_end {
        align-items: flex-end;
      }
    }

    &__exact {
      padding: 5px 5px 5px 40px;
      margin: 20px 0 10px;
      background: url(/img/checkbox.svg) 0 center no-repeat;
      background-size: 30px;
      cursor: pointer;

      &_active {
        background: url(/img/checkbox_active_violet.svg) 0 center no-repeat;
        background-size: 30px;
        color: $violet;
      }
    }

    &__schedule {
      &-item {
        padding: 5px 5px 5px 40px;
        margin-bottom: 10px;
        background: url(/img/checkbox.svg) 0 center no-repeat;
        background-size: 30px;
        cursor: pointer;

        &_blue {
          color: $blue;
        }

        &_violet {
          color: $violet;
        }

        &_active {
          &.constructor-elemmenu__schedule-item_blue {
            background: url(/img/checkbox_active_blue.svg) 0 center no-repeat;
            background-size: 30px;
          }

          &.constructor-elemmenu__schedule-item_violet {
            background: url(/img/checkbox_active_violet.svg) 0 center no-repeat;
            background-size: 30px;
          }
        }
      }
    }
  
    &__toolbar {
      color: $white;
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: -20px;
      left: 15px;
      z-index: 1;
    }

    &__template {
      padding: 1px 7px;
      border-radius: 5px 0 0 5px;
      background: $grey2;
      cursor: pointer;
      
      &:hover {
        opacity: 0.4;
      }
    }

    &__emoji {
      padding: 2px 5px 0;
      border-left: 1px solid $white;
      border-right: 1px solid $white;
      background: $grey2;
      cursor: pointer;

      &:hover {
        opacity: 0.4;
        border-left: 1px solid transparent;
        border-right: 1px solid transparent;
      }
    }

    &__counter {
      padding: 1px 6px;
      border-radius: 0 5px 5px 0;
      background: $grey2;

      &_limit {
        span {
          color: $red;
        }
      }
    }

    &__action {
      position: relative;
      margin-bottom: 30px;

      &:nth-child(3) .constructor-elemmenu__operations-item_up {
        opacity: 0.2;
        cursor: default;

        &:hover {
          box-shadow: none;
        }
      }

      &:nth-last-child(3) .constructor-elemmenu__operations-item_down {
        opacity: 0.2;
        cursor: default;

        &:hover {
          box-shadow: none;
        }
      }

      &_subscribe {
        margin-bottom: 60px;

        .constructor-elemmenu__operations {
          top: -60px;
        }
      }

      &:hover {
        .constructor-elemmenu__operations {
          display: block;
        }
      }
    }

    &__tags {
      margin-top: 10px;
    }

    &__tag {
      display: inline-block;
      padding: 5px;
      border: 1px solid $grey;
      background: $lightgrey;
      border-radius: 5px;
      text-align: center;
      font-size: 12px;
      margin: 0 10px 10px 0;

      &-txt {
        margin: 0 5px;
      }

      &-remove {
        font-size: 24px;
        display: inline-block;
        line-height: 18px;
        vertical-align: top;
        color: $grey;
        cursor: pointer;
      }

      &_removeTag {
        text-decoration: line-through;
      }
    }

    &__field {
      display: inline-block;
      padding: 5px;
      border: 1px solid $grey;
      background: $lightgrey;
      border-radius: 5px;
      text-align: center;
      font-size: 12px;
      margin: 10px 10px 0 0;

      &-txt {
        margin: 0 5px;
      }

      &-remove {
        font-size: 24px;
        display: inline-block;
        line-height: 18px;
        vertical-align: top;
        color: $grey;
        cursor: pointer;
      }

      &_removeField {
        text-decoration: line-through;
      }
    }

    &__emails {
      margin-top: 10px;
    }

    &__email {
      display: inline-block;
      padding: 5px;
      border: 1px solid $grey;
      background: $lightgrey;
      border-radius: 5px;
      text-align: center;
      font-size: 12px;
      margin: 0 10px 10px 0;

      &-txt {
        margin: 0 5px;
      }

      &-remove {
        font-size: 24px;
        display: inline-block;
        line-height: 18px;
        vertical-align: top;
        color: $grey;
        cursor: pointer;
      }
    }

    &__condition {
      &-item {
        position: relative;
        overflow: hidden;
        padding: 10px 5px;
        white-space: nowrap;
        border: 1px solid $grey;
        border-radius: 5px;
        margin-bottom: 10px;
        font-size: 12px;
        color: $grey;

        &:hover {
          border: 1px solid $green;
        }
      }

      &-text {
        display: inline-block;
        vertical-align: top;
        margin: 0 5px;
        color: $darkgrey;
        border-bottom: 1px dashed $darkgrey;
      }

      &-remove {
        position: absolute;
        right: 0;
        top: 0;
        width: 30px;
        height: 100%;
        font-size: 32px;
        line-height: 40px;
        color: $grey;
        text-align: center;
        background: $white;
        border-radius: 5px;
        cursor: pointer;
      }
    }

    &__choice {
      &-list {
        position: absolute;
        margin: -1px 0 20px;
        border: 1px solid $grey;
        border-radius: 0 0 5px 5px;
        background: $white;
        width: 100%;
        cursor: pointer;
        z-index: 1;
      }

      &-item {
        padding: 5px;
        cursor: pointer;
        
        &:hover {
          background: $lightgrey;
        }
      }
    }

    &__create {
      color: $blue;
    }
  }
}