@import "../../../styles/colors";

.constructor {
  &-leftmenu {
    position: absolute;
    top: 40px;
    left: 42px;
    z-index: 999;

    &__item {
      position: relative;
      width: 44px;
      height: 44px;
      margin-bottom: 20px;
      box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.15);
      border-radius: 5px;
      white-space: nowrap;
      line-height: 48px;
      padding-left: 44px;
      cursor: pointer;

      .constructor-leftmenu__text {
        display: none;
      }

      &:hover {
        width: auto;
        padding-right: 15px;
        box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);

        .constructor-leftmenu__text {
          display: block;
          color: $white;
        }
      }

      &_message {
        background: $white url(/img/message.svg) 12px center no-repeat;

        &:hover {
          background: $blue url(/img/message_hover.svg) 12px center no-repeat;
        }
      }

      &_action {
        background: $white url(/img/action.svg) 12px center no-repeat;

        &:hover {
          background: $orange url(/img/action_hover.svg) 12px center no-repeat;
        }
      }

      &_condition {
        background: $white url(/img/condition.svg) 12px center no-repeat;

        &:hover {
          background: $green url(/img/condition_hover.svg) 12px center no-repeat;
        }
      }

      &_delay {
        background: $white url(/img/delay.svg) 12px center no-repeat;

        &:hover {
          background: $violet url(/img/delay_hover.svg) 12px center no-repeat;
        }
      }
    }
  }
}