@import "../../../styles/colors";

.modal {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 600px;
  margin: 0 auto;
  background: $white;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.38);
  border-radius: 10px;
  padding: 16px;

  &_scroll {
    .modal-content {
      min-height: 650px;
      overflow: scroll;
    }
  }

  &_xl {
    width: 888px;
  }

  &-bg {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0,0,0, .5);
    z-index: 1001;
  }

  &-header {
    position: relative;
    padding: 0 0 16px;
    margin: 0 -16px 16px;
    text-align: center;
    border-bottom: 1px solid $grey;
  }

  &-back {
    position: absolute;
    left: 16px;
    cursor: pointer;
  }

  &-content {
    text-align: center;

    &_left {
      text-align: left;
    }
  }

  &-title {
    font-size: 24px;
  }

  &-input {
    width: 100%;
    border: 1px solid #C4C4C4;
    padding: 8px;
    border-radius: 5px;
    outline: none;
  }

  &-close {
    position: absolute;
    top: -10px;
    right: 5px;
    width: 32px;
    height: 32px;
    line-height: 32px;
    font-size: 36px;
    font-weight: 300;
    color: $grey;
    cursor: pointer;
  }

  &-back {
    position: absolute;
    left: 5px;
    width: 32px;
    height: 32px;
    line-height: 32px;
    font-size: 36px;
    font-weight: 300;
    color: $grey;
    cursor: pointer;
  }

  &-footer {
    position: relative;
    padding: 16px 0 0;
    margin: 16px -16px 0;
    text-align: center;
    border-top: 1px solid $grey;

    &-title {
      color: $black;
      border-bottom: 1px dashed $black;
      cursor: pointer;
    }
  }

  &-settings {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-confirm {
    margin-left: 20px;
    padding: 19px 0px 14px 40px;
    background: url(/img/checkbox.svg) 0 14px no-repeat;
    background-size: 30px;
    cursor: pointer;

    &_active {
      background: url(/img/checkbox_active_blue.svg) 0 14px no-repeat;
      background-size: 30px;
    }
  }

  &-buttons {
    display: flex;
    justify-content: center;

    .modal-button {
      margin: 0 8px;
    }
  }
}