.userinfo {
  box-shadow: -6px 0px 10px -2px rgba(196, 196, 196, 0.4);
  z-index: 10;
  position: relative;
  padding: 0 20px;
  max-height: calc(100vh - 75px);

  &-section {
    padding: 30px 0;
    min-width: 300px;
    border-bottom: 1px solid rgba(207, 207, 207, 0.7);

    &-title {
      font-size: 14px;
      line-height: 1;
      margin-bottom: 20px;
    }
  }
}

.user-basic {
  display: flex;
  align-items: center;

  &-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &-avatar {
    display: block;
    width: 70px;
    height: 70px;
    margin-right: 20px;
  }

  &-actions {
    display: flex;
  }

  &-action {
    padding: 0;
    cursor: pointer;
    border-bottom: 1px dashed currentColor;
    border-radius: 0;
  
    &:hover {
      text-decoration: none;
    }
  
    &:focus {
      outline: 0;
      box-shadow: none;
      text-decoration: none;
    }
  
    + .user-basic-action {
      margin-left: 20px;
    }
  
    &.is-unsubscribe {
      color: #CCCCCC;
  
      &:hover {
        color: #000000;
      }
    }
  
    &.is-delete {
      color: red;
  
      &:hover {
        color: darken(red, 15%);
      }
    }
  }
}

.user-tag {
  display: inline-block;
  padding: 5px;
  border: 1px solid #D0D0DA;
  background: #F4F4F4;
  border-radius: 5px;
  text-align: center;
  font-size: 12px;
  margin: 0 10px 10px 0;

  &-text {
    margin: 0 5px;
  }

  &-remove {
    font-size: 24px;
    display: inline-block;
    line-height: 18px;
    vertical-align: top;
    color: #D0D0DA;
    cursor: pointer;
  }

  &:last-child {
    margin: 0;
  }
}

.user-subscriptions {
  margin-top: 20px;
}

.user-subscription {
  &:not(:last-child) {
    margin-right: 10px;
    margin-bottom: 10px;
  }
}

