@import "../styles/colors";

.settings {
  &-section {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid $lightgrey;

    &__title {
      font-weight: 500;
      margin-bottom: 15px;
    }

    &__label {
      font-weight: 500;
    }

    &__card {
      margin-left: 15px;
      padding-left: 40px;

      &_mastercard {
        background: url(/img/mastercard.svg) 0 center no-repeat;
      }
    }
  }

  &-input {
    display: block;
    padding: 10px 20px;
    border: 1px solid #ccc;
    margin: 10px 0;
    border-radius: 5px;
    outline: none;
  }

  &-tariff {
    display: flex;
    align-items: center;

    &__current {
      border: 1px solid $lightgrey;
      border-radius: 10px;
      margin-right: 20px;
    }

    .settings-section__label {
      margin-left: 15px;

      &:first-child {
        margin-left: 10px;
      }
    }
  }
}