@import "../../../styles/colors";

.select {
  min-width: 200px;
  position: relative;
  
  &-create {
    font-size: 14px;
    padding: 10px;
    text-align: center;
    background: $lightgrey;
    border-radius: 0 0 5px 5px;
  }

  &_answer {
    .select-list {
      width: 322px;

      &_newfield {
        width: 269px;
      }
    }
  }

  &_delay {
    width: 220px;

    .select-list {
      width: 220px;
    }
  }

  &_step {
    width: 340px;
    padding: 10px;

    .select-list {
      width: 320px;
    }
  }

  &_step_error {
    width: 340px;
    padding: 10px;

    .select-title {
      box-shadow: 0 0 0 1px $red;
    }

    .select-list {
      width: 320px;
    }
  }

  &_settings {
    width: 288px;
  }

  &_tags {
    padding: 20px;

    .select-list {
      width: 160px;
    }
  }

  &_fields {
    .select-list {
      &_newfield {
        width: 235px;
      }
    }
  }

  &_datetime {
    min-width: 150px;
    margin-bottom: 20px;
  }

  &-title {
    position: relative;
    padding: 7px 10px 7px;
    border: 1px solid $grey;
    font-size: 14px;
    border-radius: 5px;
    width: 100%;
    cursor: pointer;

    &:before {
      content: '⌃';
      position: absolute;
      font-size: 16px;
      top: 1px;
      right: 5px;
      transform: scale(1, -1);
    }

    &_open {
      border-radius: 5px 5px 0 0;

      &:before {
        top: 8px;
        transform: scale(1, 1);
      }
    } 

    &_error {
      box-shadow: 0 0 0 1px $red;
    }
  }

  &-list {
    position: absolute;
    margin: -1px 0 20px;
    border: 1px solid $grey;
    border-radius: 0 0 5px 5px;
    background: $white;
    width: 288px;
    cursor: pointer;
    z-index: 2;

    &_newfield {
      bottom: -30px;
      right: 10px;
    }
  }

  &-item {
    font-size: 14px;
    padding: 10px;

    &:hover {
      background: $lightgrey;
      color: $blue;
    }
  }

  &-form {
    padding: 10px;
  }

  &-text {
    font-size: 14px;
  }

  &-info {
    position: relative;
    margin-right: 10px;
    width: 24px;
    text-align: center;
    border-radius: 20px;
    font-size: 14px;
    background: $grey;
    color: $white;

    &:hover {
      .select-tooltip {
        display: block;
      }
    }
  }

  &-tooltip {
    display: none;
    position: absolute;
    padding: 5px 10px;
    background: $grey;
    color: $white;
    top: 35px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 12px;
    border-radius: 5px;
    z-index: 1;

    &:before {
      content: '';
      position: absolute;
      left: 50%;
      margin-left: -10px;
      top: -7px;
      width: 0;
      height: 0;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      border-bottom: 10px solid $grey;
    }
  }
}