.table {
  font-size: 14px;
  width: 100%;
  display: block;
  position: relative;

  &-row {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: 1px solid rgba(#cfcfcf, .7);
    transition: transform 100ms linear;

    &_disabled {
      opacity: 0.5;
    }
  }

  &-head-row {
    background-color: #FAFAFA;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
}

.name-column {
  width: 40%;
}

.date-column {
  width: 20%;
}

.count-column {
  width: 10%;
}

