@import "../../../styles/colors";

.tariff-container {
  padding: 22px 22px 22px 32px;
  text-align: center;
  background: rgba($blue, 0.05);
  margin: 0 -6px 0 -36px;
}

.tariff-title {
  font-weight: 500;
  color: #929292;
  margin-bottom: 7px;
  line-height: 1;
}

.tariff-item {
  &:not(:last-child) {
    margin-bottom: 16px;
  }
}
