.messengers-list {
  margin-top: 40px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.messengers-list-description {
  width: 100%;
  text-align: center;
  font-size: 16px;
}

.messenger {
  border: 1px solid #C4C4C4;
  border-radius: 5px;
  padding: 23px 6px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 150px;

  &:not(:last-child) {
    margin-right: 20px;
  }

  &:hover {
    cursor: pointer;

    background-color: rgba(#00baff, .1);
    border-color: #00baff;
  }
}

.messenger-icon {
  margin-bottom: 10px;
}

.account-creation {
  margin-top: 30px;

  border-top: 1px solid rgba(#CFCFCF, .7);

  padding-top: 30px;
  padding-bottom: 30px;
}

.account-creation-title {
  font-size: 14px;
  margin-bottom: 20px;
}

.accounts-list {
  display: flex;
}

.accounts-list-item,
.accounts-list-item-new {
  border: 1px solid #C4C4C4;
  width: 150px;
  min-height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  &:hover {
    background-color: rgba(#00baff, .1);
    border-color: #00baff;
  }

  &:not(:last-child) {
    margin-right: 20px;
  }
}

.accounts-list-item-new {
  color: #929292;
  font-size: 10px;

  &:hover {
    color: #929292;
  }
}
.accounts-list-item-new-icon {
  color: #C4C4C4;
  margin-bottom: 10px;
}
.accounts-list-item-footer {
  display: flex;
  align-items: center;
  font-size: 10px;
  color: #929292;
  margin-top: auto;
  width: 100%;
  padding: 5px 10px;
}
.accounts-list-item-type {
  margin-right: 6px;
  min-width: 10px;
}
.accounts-list-item-info {
  margin-top: auto;
}

.accounts-list-item_active {
  background-color: rgba(#00baff, .1);
  border-color: #00baff;
}
