.filter {
  position: relative;
  height: 37px;
  display: inline-flex;
  width: 300px;
  border: 1px solid #929292;
  border-radius: 5px;

  &-button {
    width: 40px;
    background: url('/img/filter.svg') no-repeat center;
    border-right: 1px solid #CFCFCF;
    cursor: pointer;
  }

  &-search {
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 35px;
    background: url(/img/search.svg) no-repeat center;
    cursor: pointer;
  }

  &-input {
    padding: 8px;
    border: none;
    width: 100%;
    border-radius: 5px;
    outline: none;

    &_number {
      border: 1px solid #CFCFCF;
      width: 50px;
    }
  }

  &-title {
    text-align: center;
    font-weight: 400;
    padding: 10px;
    color: #071F27;
  }

  &-subtitle {
    font-size: 12px;
    padding: 10px 5px;
    border-top: 1px solid #929292;
    color: #929292;
  }

  &-menu {
    position: absolute;
    top: 40px;
    left: 0;
    position: absolute;
    background: #FFFFFF;
    border-radius: 5px;
    z-index: 3;
    border: 1px solid #929292;
  }

  &-item {
    padding: 7px 20px;
    font-size: 14px;
    cursor: pointer;

    &:hover {
      background: #F4F4F4;
    }
  }

  &-list {
    margin-top: 20px;

    &-item {
      position: relative;
      display: inline-block;
      padding: 5px 30px 5px 10px;
      border-radius: 5px;
      border: 1px solid #ccc;
      margin: 0 10px 10px 0;
    }
  }

  &-remove {
    position: absolute;
    right: 0;
    top: 0;
    height: 33px;
    text-align: center;
    font-size: 20px;
    width: 20px;
    cursor: pointer;
  }
}
